

Office.onReady(() => {
  const radios = document.querySelectorAll("input[name='duration']");
  const halfDaySection = document.getElementById("half-day-section");
  const dateSection = document.getElementById("date-section");
  const endDateSection = document.getElementById("end-date-section");
  const typeSection = document.getElementById("type-section");
  const submitBtn = document.getElementById("submit-btn");

  radios.forEach(r => {
    r.addEventListener("change", () => {
      halfDaySection.classList.add("hidden");
      endDateSection.classList.add("hidden");
      dateSection.classList.remove("hidden");
      typeSection.classList.remove("hidden");

      if (r.value === "half") {
        halfDaySection.classList.remove("hidden");
      } else if (r.value === "multi") {
        endDateSection.classList.remove("hidden");
      }
    });
  });

  const checkValid = () => {
    const duration = document.querySelector("input[name='duration']:checked");
    const startDate = document.getElementById("start-date").value;
    const endDate = document.getElementById("end-date").value;
    const leaveType = document.getElementById("leave-type").value;
    const half = document.querySelector("input[name='half-type']:checked");

    if (!duration || !startDate || !leaveType) return false;
    if (duration.value === "multi" && !endDate) return false;
    if (duration.value === "half" && !half) return false;

    return true;
  };

  document.querySelectorAll("input, select").forEach(el => {
    el.addEventListener("change", () => {
      submitBtn.classList.toggle("hidden", !checkValid());
    });
  });

  submitBtn.addEventListener("click", async () => {
    const duration = document.querySelector("input[name='duration']:checked").value;
    const half = document.querySelector("input[name='half-type']:checked")?.value;
    const type = document.getElementById("leave-type").value;
    const start = document.getElementById("start-date").value;
    const end = document.getElementById("end-date").value || start;

    const startDate = new Date(start);
    const endDate = new Date(end);
    const user = Office.context.mailbox.userProfile.displayName;

    let subject = `${user} - ${type}`;
    let durationDays = 1;

    if (duration === "multi") {
      const timeDiff = Math.ceil((endDate - startDate) / (1000 * 60 * 60 * 24)) + 1;
      durationDays = timeDiff;
    }

    subject += ` (${durationDays} jour${durationDays > 1 ? 's' : ''})`;

    const appointment = {
      subject,
      start: new Date(startDate),
      end: new Date(endDate),
      location: "",
      body: {
        contentType: "HTML",
        content: `${user} est en cong√© (${type})`
      },
      requiredAttendees: [],
      optionalAttendees: [],
      reminder: { minutesBeforeStart: 15 }
    };

    if (duration === "half") {
      if (half === "AM") {
        appointment.start.setHours(0, 0);
        appointment.end.setHours(14, 0);
      } else {
        appointment.start.setHours(14, 0);
        appointment.end.setHours(23, 59);
      }
    } else {
      appointment.start.setHours(0, 0);
      appointment.end.setHours(23, 59);
    }

    Office.context.mailbox.displayAppointmentForm(appointment);
  });
});

